@import "../../../../App.scss";
@import "../../../../mixin.scss";

.CustomerCard-pc {
}

.CustomerCard-sp {
  width: 100%;
  background-color: $base-color-thin;
  display: grid;
  padding: 9px 0;
  &__row {
    @include flex_box();
    padding: 9px 0 9px 9px;
    &.row1 {
      > .customer-name {
        flex: 1;
        word-break: break-all;
      }
      > .col {
        width: 128px;
        @include flex_box();
        @include flex_content_around();
        @include flex_align_center();
        margin-left: auto;
        > .customer-rank,
        .customer-type {
          width: 52px;
          text-align: center;
          color: $font-color2;
          background-color: $bg-color4;
        }
      }
    } // row1
    &.row2 {
      > .customer-postal {
        margin-right: 10px;
        word-break: break-all;
      }
      > .customer-address {
        word-break: break-all;
      }
    }
    &.row3 {
      > .customer-tel {
        width: 170px;
      }
      > .customer-salesStaff {
        flex: 1;
        word-break: break-all;
      }
    } // row3
  }
}
