@import "../../../../App.scss";
@import "../../../../mixin.scss";

$footer-height: 56px;

.CustomerDetailSP {
  height: 100%;
  &__body {
    height: calc(100% - 56px);
    &__header {
      @include flex_box();
      @include flex_align_center();
      height: 40px;
      padding: 0 10px;
      background-color: $base-color;
      /* ヘッダーのborder-bottomと色が被って縦方向中央揃いになってないように見える対応。 */
      border-bottom: solid 2px $border-color2;
      > span {
        color: $font-color2;
      }
      > .button {
        width: 65px;
        margin-left: auto;
      }
    } // CustomerDetailSP__body__header

    &__contents {
      height: calc(100% - 40px);
      padding-top: 5px;
      > div {
        height: 100%;
        > .tabular.menu {
          > .item {
            width: calc(100% / 5);
            padding: 0;
            font-size: 12px;
            @include flex_box();
            @include flex_content_center();
            &:nth-child(5) {
              // font-size: 11px;
            }  
          }
        }
        > .bottom.attached.segment {
          height: calc(100% - 2.85714286em);
          overflow: auto;
          padding: 0;
          @include scroll_display_none();
        }
      }
    } // CustomerDetailSP__body__contents
  }
  &__footer {
    @include flex_box();
    @include flex_align_center();
    height: 56px;
    border-top: solid 2px $border-color2;
    > .button-wrapper {
      padding: 0 10px;
      &.share,
      &.regist-support {
        flex: 2;
      }
      &.phone,
      &.upload {
        flex: 1;
      }
      .IconButton {
        margin: auto;
        height: 35px;
      }
      &.regist-support > .button {
        margin: auto;
        height: 35px;
        width: 90px;
      }
    }
  }
}
