@import "../../../../App.scss";
@import "../../../../mixin.scss";

.CustomerSP {
  height: 100%;
  /* TODO クラス名の調整 */
  .MapSP {
    &__body {
      height: 100%;
      position: relative;
      &__map {
      }
      .button {
        border: solid 1px $border-color2;
        color: $font-color3;
        background-color: $bg-color2;
        font-weight: normal;
        width: 110px;
        &.search-address {
          position: absolute;
          bottom: 10px;
          left: 10px;
          z-index: 10;
        }
        &.change-display {
          position: absolute;
          bottom: 10px;
          right: 10px;
          z-index: 10;
        }
      } //MapSP__body__map
    } // MapSP__body

    &__footer {
      height: 102px;
      width: 100%;
      border-top: solid 2px $border-color2;
      &__row {
        &.row1 {
          @include flex_box();
          @include flex_content_around();
          @include flex_align_center();
          height: 50%;
          .btn {
            width: 110px;
            &.ob > span {
              width: 100%;
              text-align: center;
            }
          }
        }
        &.row2 {
          @include flex_box();
          @include flex_content_around();
          @include flex_align_center();
          height: 50%;
          .btn {
            width: 140px;
          }
        }
        > .btn {
          background-color: $base-color;
          color: $font-color2;
          font-weight: normal;
          height: 35px;
        }
      }
    } //MapSP__footer
  } // MapSP
}
