@import "./mixin.scss";

#root {
  height: 100%;
  .App {
    // text-align: center;
    height: 100%;
  }
}

/* color */
$base-color: #1451a1;
$base-color-thin: #f3f6fa;
$base-color: #1451A1;

$bg-color1: #ffffff;
$bg-color2: $base-color-thin;
$bg-color3: $base-color;
$bg-color4: #5f6368;
$bg-color5: #5f97e1; // 指定外
$hover-color: #dfe1e5;

$font-color1: #5f6368;
$font-color2: #ffffff;
$font-color3: $base-color;

$border-color1: #cccccc;
$border-color2: $base-color;

$emphasis-color1: #f56922;
$emphasis-color2: #ebb521;
$emphasis-color3: #de2a7b;

.btn-color-set1 {
  background-color: $base-color-thin !important;
  color: $base-color !important;
  border: 1px solid $base-color !important;
}

/* 「すべて」ボタン用スタイル */
.btn-filter-all {
  padding: 0;
  > .img-wrapper {
    width: 54px;
  }
  img {
    width: 20px;
    padding-right: 1px;
  }
}

/* semantic */
.ui.button {
  margin-right: 0;
  padding: 0;
  min-height: 30px;
  min-width: 50px;
  &:hover {
    background-color: $hover-color !important;
  }
}
