@import "../../../../App.scss";
@import "../../../../mixin.scss";

.PropositionCard {
}

.PropositionCard-sp {
  width: 100%;
  background-color: $base-color-thin;
  display: grid;
  padding: 9px 0;
  &__row {
    @include flex_box();
    padding: 3px 0 3px 9px;
    &.row1 {
      > .col1 {
        flex: 1;
        > div {
          word-break: break-all;
          height: 50%;
          @include flex_box();
          @include flex_align_center();
        }
      }
      > .col2 {
        width: 60px;
        margin-left: auto;
        > div {
          height: 28px;
          @include flex_box();
          // @include flex_align_center();
          > span {
            width: 52px;
            text-align: center;
            color: $font-color2;
            background-color: $bg-color4;
            margin: auto;
            padding: 2px 0;
          }
        }
      }
      > .col3 {
        @include flex_box();
        @include flex_content_around();
        @include flex_align_center();
        width: 65px;
        height: 50px !important;
        // margin-left: auto;
      }
    } // row1
    &.row2 {
      > .address {
        word-break: break-all;
      }
    }
    &.row3 {
      > .tel {
        width: 170px;
      }
      > .salesStaff {
        flex: 1;
        word-break: break-all;
        text-align: center;
      }
    } // row3
  }
}
