@import "../../../../../App.scss";
@import "../../../../../mixin.scss";

.BaseListSP {
  width: 100%;
  height: 100%;
  overflow: auto;
  @include scroll_display_none();
  .card-wrapper {
    border-top: solid 2px $border-color1;
    &:last-child {
      border-bottom: solid 2px $border-color1;
    }
    &:nth-child(odd) {
      .card {
        background-color: $bg-color1;
      }
    }
  }
}
