@import "../../../../App.scss";
@import "../../../../mixin.scss";

.MaintenanceCard-pc {
}

.MaintenanceCard-sp {
  width: 100%;
  background-color: $base-color-thin;
  padding: 9px 0;
  > .row1,
  .row2,
  .row3,
  .row4,
  .row5 {
    @include flex_box();
    padding: 4px 9px;
  }
  .row1 {
    &__col1 {
      flex: 1;
    }
    &__col2,
    &__col3 {
      width: 64px;
      > span {
        display: block;
        width: 60px;
        background-color: $bg-color4;
        color: $font-color2;
        text-align: center;
        margin-left: auto;
      }
    } // row1__col2, row1__col3
  } // row1

  .row2,
  .row3 {
    word-break: break-all;
  } // row2, row3

  .row4 {
    &__col1 {
      width: 82px;
    }
    &__col2 {
      flex: 1;
      word-break: break-all;
    }
  } // row4

  .row5 {
    &__col1 {
      width: 160px;
    }
    &__col2 {
      width: 90px;
      text-align: right;
    }
    &__col3 {
      flex: 1;
      word-break: break-all;
    }
  } // row5
}
