@import "../../../../App.scss";
@import "../../../../mixin.scss";

.SupportHistoryCard {
}

.SupportHistoryCard-sp {
  width: 100%;
  background-color: $base-color-thin;
  padding: 9px 0;
  &__row {
    @include flex_box();
    @include flex_align_center();
    height: 28px;
    padding: 0 9px;
    
    &.row1 {
      > .col1 {
        flex: 1;
        > div {
          word-break: break-all;
          @include flex_box();
          @include flex_align_center();
        }
      }
      > .col2 {
        width: 100px;
        > div {
          width: 100%;
          text-align: right;
        }
      }
    } // row1

    &.row2 {
      > .col1 {
        flex: 1;
        > div {
          word-break: break-all;
          @include flex_box();
          @include flex_align_center();
        }
      }
      > .col2 {
        width: 100px;
        > div {
          > span {
            display: block;
            width: 70px;
            text-align: center;
            color: $font-color2;
            background-color: $bg-color4;
            margin-left: auto;
            padding: 2px 0;
          }
        }
      }
    } // row2

    &.row3,
    &.row4 {
      > div {
        flex: 1;
        word-break: break-all;
      }
    } // row3, row4
  }
}
