@import "../../../../mixin.scss";
@import "../../../../App.scss";

.CustomerInfoWindow {
  width: 100%;
  position: fixed;
  bottom: 0;
  z-index: 20;
  &__arrow-btn {
    background-color: $bg-color3;
    color: $font-color2;
    text-align: center;
  }

  &__info {
    background-color: $bg-color1;
    padding: 9px 0;
    &__row {
      @include flex_box();
      padding: 9px 0 9px 9px;
      &.row1 {
        > .customer-name {
          flex: 1;
          word-break: break-all;
        }
        > .col {
          width: 128px;
          @include flex_box();
          @include flex_content_around();
          @include flex_align_center();
          margin-left: auto;
          > .customer-rank,
          .customer-type {
            width: 52px;
            text-align: center;
            color: $font-color2;
            background-color: $bg-color4;
          }
        }
      } // row1
      &.row2 {
        > .customer-address {
          word-break: break-all;
        }
      }
      &.row3 {
        > .customer-tel {
          width: 170px;
        }
        > .customer-salesStaff {
          flex: 1;
          word-break: break-all;
        }
      } // row3
    } // CustomerInfoWindow__info
  }

  &__footer {
    height: 52px;
    background-color: $bg-color5;
    border-top: solid 1px $border-color2;
    @include flex_box();
    @include flex_content_around();
    @include flex_align_center();
    .button {
      width: 90px !important;
      height: 34px !important;
      padding: 0;
      background-color: $bg-color2 !important;
      border: none !important;
      box-shadow: 0 0 2px;
      &:hover {
        background-color: $hover-color !important;
        box-shadow: 0 0 2px;
      }
      .fa-mobile-alt {
        font-size: 28px;
      }
      .fa-line {
        font-size: 31px;
      }
    }
  } // CustomerInfoWindow__info
} // CustomerInfoWindow
