@import "../../../../App.scss";
@import "../../../../mixin.scss";

.FileCard-pc {
}

.FileCard-sp {
  width: 100%;
  background-color: $base-color-thin;
  padding: 9px 0;
  > .row1,
  .row2,
  .row3 {
    @include flex_box();
    padding: 0 9px;
  }
  .row1 {
    &__col1 {
      flex: 1;
      > div {
        word-break: break-all;
        padding: 4px 0;
      }
      &__row3 {
        @include flex_box();
        &__col1 {
          width: 56px;
        }
        &__col2 {
          flex: 1;
        }
      } // row1__col1__row3
    } // row1__col1
    &__col2 {
      width: 90px;
      text-align: right;
      > img {
        height: 50px !important;
        width: 68px;
      }
      > .img-display {
        width: 68px;
      }
    } // row1__col2
  } // row1

  .row2 {
    > div {
      word-break: break-all;
      padding: 4px 0;
    }
    &__col1 {
      width: 56px;
    }
    &__col2 {
      flex: 1;
    }
  } // row2

  .row3 {
    > div {
      padding: 4px 0;
    }
    &__col1 {
      width: 151px;
    }
    &__col2 {
      flex: 1;
      word-break: break-all;
      text-align: right;
    }
  } // row3
}
