@import "../../../../App.scss";
@import "../../../../mixin.scss";

.CustomerInfoTabContents {
  padding: 14px;
  &__row {
    @include flex_box();
    padding-bottom: 10px;
    &.row1 {
      .col1 {
        @include flex_box();
        @include flex_align_center();
      }
      .col2 {
        margin-left: auto;
      }
    }
    &.row2 {
      .col1 {
        width: 56px;
      }
      .col2 {
        flex: 1;
        word-break: break-all;
      }
    }
    &.row3 {
      .col1 {
        width: 112px;
      }
      .col2 {
        flex: 1;
        word-break: break-all;
      }
    }
    &.row4 {
      .col1 {
        width: 42px;
      }
      .col2 {
        flex: 1;
        .row2 {
          word-break: break-all;
        }
      }
    }
    &.row6 {
      .col1 {
        width: 112px;
      }
      .col2 {
        @include flex_box();
        flex: 1;
        word-break: break-all;
      }
    }
  } // CustomerInfoTabContents__row
} // CustomerInfoTabContents
